// ** Redux Imports
import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'

// ** Axios Imports
import axios from 'axios'

export const getAllData = createAsyncThunk('appUsers/getAllData', async () => {
  const config = {
    headers: { Authorization: 'Basic c2luZ3VsYToxMjJ3YXMxMjI='}
  }

  const response = await fetch('/api/manager/users/students/',  config).then(res => res.json())
  return response.results
})

export const getData = createAsyncThunk('appUsers/getData', async params => {

  params = new URLSearchParams(params)

  const config = {
    headers: { Authorization: 'Basic c2luZ3VsYToxMjJ3YXMxMjI='}
  }
  const response = await fetch(`/api/manager/users/students/?${params}`,  config).then(res => res.json())

  return {
    params,
    data: response.results,
    totalPages: response.count
  }
})

export const getUser = createAsyncThunk('appUsers/getUser', async id => {
  const response = await axios.get('/api/users/user', { id })
  return response.data.user
})


export const createUser = async (userData) => {
  const config = {
    method: 'POST',
    headers: { 
      Authorization: 'Basic c2luZ3VsYToxMjJ3YXMxMjI=',
      'Content-Type': 'application/json'
    },
    body: JSON.stringify(userData)
  }

  const response = await fetch(`/api/manager/users/add/`,  config)
  if (response.status > 201) {
    return {
      error: true,
      data: await response.json()
    }
  } else {
    return {
      error: false,
      data: await response.json()
    }
  }
}


export const addUser = createAsyncThunk('appUsers/addUser', async (user, { dispatch, getState }) => {

  const config = {
    method: 'POST',
    headers: { 
      Authorization: 'Basic c2luZ3VsYToxMjJ3YXMxMjI=',
      'Content-Type': 'application/json'
    },
    body: JSON.stringify(user)
  }

  console.log(config)

  const response = await fetch(`/api/manager/users/add/`,  config).then(res => res.json())
  return response
})

export const deleteUser = createAsyncThunk('appUsers/deleteUser', async (id, { dispatch, getState }) => {
  await axios.delete('/apps/users/delete', { id })
  await dispatch(getData(getState().users.params))
  await dispatch(getAllData())
  return id
})

export const appUsersSlice = createSlice({
  name: 'appUsers',
  initialState: {
    data: [],
    total: 1,
    params: {},
    allData: [],
    selectedUser: null
  },
  reducers: {},
  extraReducers: builder => {
    builder
      .addCase(getAllData.fulfilled, (state, action) => {
        state.allData = action.payload
      })
      .addCase(getData.fulfilled, (state, action) => {
        state.data = action.payload.data
        state.params = action.payload.params
        state.total = action.payload.totalPages
      })
      .addCase(getUser.fulfilled, (state, action) => {
        state.selectedUser = action.payload
      })
  }
})

export default appUsersSlice.reducer
